import wButton from '@/components/watch-login/watch-button.vue' //button

// 注册,发送验证码方法：
import {resetPwd,sendCode} from '@/network/login'

export default {
	components:{
		wButton
	},
	data() {
		return {
			windowHeight:0,//屏幕高度
			phone:'',//手机号
			password:'',//密码
			code:'',//验证码
			isRotate: false, //是否加载旋转
			modalName:'',//忘记密码
			codeTip:"获取验证码",
			currentTime: '60', //倒数计时
			getCodebutton:false,//防止重复提交
		}
	},
	onLoad() {
		var that = this
		uni.getSystemInfo({
		    success: function (res) {
				that.windowHeight = res.windowHeight
		        console.log('屏幕高度为'+res.windowHeight);
		    }
		});
	},
	
	methods: {
		// 获取验证码倒计时
		getCode(){
			var that = this;
			var currentTime = that.currentTime;
			if((/^1[3456789]\d{9}$/).test(that.phone)){
				if(that.getCodebutton){ return }
				that.getCodebutton = true
				
				// 发送验证码
				sendCode(that.phone).then(res => {
					if(res.data.code == 200){
						var interval = setInterval(function() {
							that.codeTip = (currentTime - 1) + 's'
							currentTime--;
							if (currentTime <= 0) {
								clearInterval(interval)
								that.codeTip = '获取验证码'
								that.getCodebutton = false
							}
						}, 1000)
					}
				})
				
			}else{
				uni.showToast({
					title:'请输入正确的手机号',
					icon:'none'
				})
			}
		},
		// 改密方法
		startLogin(){
			var that = this
			// 防抖动
			if(that.isRotate) return
			if(!(/^1[3456789]\d{9}$/.test(that.phone))){
				uni.showToast({
					title:'手机号格式有误',
					icon:'none'
				})
				return
			}
			console.log(that.password.length)
			if(that.password.length < 6){
				uni.showToast({
					title:'密码最少为6位',
					icon:'none'
				})
				return
			}
			console.log(that.code.length)
			if(!that.code.length){
				uni.showToast({
					title:'请输入验证码',
					icon:'none'
				})
				return
			}
			that.isRotate = true
			setTimeout(function() {that.isRotate = false}, 5000);
			let data = {
				phone:that.phone,
				pwd:that.password,
				code:that.code
			}
			
			resetPwd(data).then(res => {
					if(res.data.code == 200){
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
						uni.navigateTo({
							url:"login"
						})
					}else{
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}
			})
		},
	}
}
